import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { Icon, Affix } from 'antd';
import Link from 'gatsby-link';

import Footer from '../components/Footer';
import Header from '../components/Header';
import Navigation from '../components/Navigation';
import './index.less';
// 
import '../theme/red.less';
import '../theme/blue.less';
import '../theme/black.less';
import '../theme/magenta.less';

class TemplateWrapper extends Component {

  state = {
    showNavigationBar: false,
    lock: true,
    style: {
      top: -80
    }
  }

  componentWillMount() {
    global.addEventListener('scroll', this.handleScroll);
    global.document.body.addEventListener('mousewheel', this.handleMousewheel);
  }

  handleScroll = () => {
    // 
    if (window.scrollY == 0 && this.state.lock == true) {
      setTimeout(() => {
        // 
        if (window.scrollY == 0) {this.setState({lock: false})}
      }, 300);
    } else {
      // 
      if (!this.state.lock) {this.setState({lock: true});}
    }
  }

  handleShowNavigationBar = () => {
    this.setState({
      showNavigationBar: !this.state.showNavigationBar,
      style: {
        top: this.state.showNavigationBar ? -80 : 0
      }
    });
  }

  handleMousewheel = e => {
    let top = e.wheelDelta;
    let { style } = this.state;
    // 
    if (e.wheelDelta > 0) {
      // 
      if (!this.state.lock) {
        //   120  
        if (top > 480) {
          this.setState({style: {top: 0}, lock: true});
        }
      }
    } else {
      this.setState({style: {top: -80}});
    }
  }

  render () {
    return (
      <div className="red-body" id="body">
        <Helmet
          title="lighted-mirror"
          meta={[
            { name: 'description', content: 'lighted-mirror' },
          ]}
        />
        <Header/>
        <Navigation showNavigationBar={this.state.showNavigationBar}/>
        <div className="main-container" style={this.state.style}>
          <Affix>
            <button className="bar" onClick={this.handleShowNavigationBar}></button>
          </Affix>
          <div className="content-container">
            {this.props.children()}
          </div>
        </div>
        <Footer/>
      </div>
    )
  }
}


